

.Partners {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding-bottom: 40px;
    max-height: 80vh;
    .partner {
        margin: 5px;
        width: 220px;
        height: 100px;
        background: center center no-repeat;
        background-size: contain;
        @media screen and (max-width: 768px) {
            width: 165px;
            height: 75px;
        }
        @media screen and (max-width: 490px) {
            width: 110px;
            height: 50px;
        }
    }
}
